  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        lists: [],
				listss:[],
        current: 0,
        curIndex: null,
        sliderAnim: {},
				isLogin: this.$store.state.isLogin,
				pay_type:'123',
				status:'123',
				add_time:'123',
				money:'123'
      }
    },
    onLoad() {
      // let id = uni.getStorageSync('user').id
			// let id = JSON.parse(uni.getStorageSync('userInfo')).id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
      uni.request({
        url: urls.HTTP + '/user/usermessage',
        method: 'POST',
        data: {
          id: id
        },
        success: (res) => {
          if (res.data.code === 0) {
            console.log('用户通知', res.data.message)
            this.lists = res.data.message
          } else {
            uni.showToast({
              title: res.data.error,
              icon: 'none'
            })
          }
        }
      })
    },
    onShow (){
      // 创建一个动画实例
    	var animation = uni.createAnimation({
    		duration: 500,
        timingFunction: 'ease'
    	})
      
      this.animation = animation
    },
    methods: {
      changeClass: function (n) {
        this.current = n
        // let id = uni.getStorageSync('user').id
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        if (n === 0) {
          this.animation.left(0).step()
          this.sliderAnim = this.animation.export()
          uni.request({
            url: urls.HTTP + '/user/usermessage',
            method: 'POST',
            data: {
              id: id
            },
            success: (res) => {
              if (res.data.code === 0) {
                // console.log('用户通知', res.data.message)
                this.lists = res.data.message
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
              }
            }
          })
        } else {
          this.animation.left(100 + '%').step()
          this.sliderAnim = this.animation.export()
          uni.request({
            url: urls.HTTP + '/user/usermessage',
            method: 'POST',
            data: {
              id: id
            },
            success: (res) => {
              if (res.data.code === 0) {
                // console.log('用户通知', res.data.message)
                this.lists = res.data.message
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
              }
            }
          })
        }
				
				if(n==1){
					uni.request({
						url: urls.HTTP + '/user/systemmessage',
						method: 'POST',
						data: {
						  uid: id
						},
						success:(res) =>{
							console.log(res)
							this.listss = res.data.message
							// this.pay_type = res.data.message.pay_type
							// this.status = res.data.message.status
							// this.add_time = res.data.message.add_time
							// this.money = res.data.message.money
						}
					})
				}
      },
      goDetail: function (val, i) {
        let id = val.id
        uni.request({
          url: urls.HTTP + '/user/messagedetail',
          method: 'POST',
          data: {
            id: id
          },
          success: (res) => {
            // console.log(res)
            if (res.data.code === 0) {
              this.$store.dispatch('comment', res.data.commet[0])
              uni.navigateTo({
                url: 'page/myReply'
              })
            } else {
              uni.showToast({
                title: res.data.error,
                icon: 'none'
              })
            }
          }
        })
        this.curIndex = i
      },
      delThis: function (item) {
        uni.showModal({
          title: '提示',
          content: '是否删除此通知？',
          success: (res) => {
            if (res.confirm) {
              uni.showToast({
                title: '删除成功!'
              })
            }
          }
        })
      },
      noRead: function (i) {
        if (i === this.curIndex) {
          return false
        } else {
          return true
        }
      },
      imgUrl: function (url) {
        return urls.UrlHttp + url
      }
    },
    computed: {
      isOne () {
        if (this.current === 0) {
          return true
        } else if (this.current === 1) {
          return false
        }
      }
    }
  }
